*,
*::before,
*::after {
  box-sizing: border-box;
}
body,
html {
  height: 100%;
}
body * {
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  padding: 0;
  min-height: 100%;
  background-color: #ffffff;
  color: #000;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.wrapper {
  width: 90%;
  max-width: 1920px;
  margin: auto;
}
