.overlay {
  opacity: 0;
}
.overlay.open {
  opacity: 1;
  transition: opacity 0.2s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  background-color: black;
}
.overlay ul {
  position: fixed;
  display: none;
  top: 35%;
}
.overlay.open ul {
  display: unset;
}
.overlay.open li {
  font-size: calc(40px + (52 - 40) * ((100vw - 300px) / (1000 - 300)));
  font-weight: 500;
  list-style: none;
  padding-bottom: 15px;
}
.overlay.open a {
  text-decoration: none;
  color: #fff;
  opacity: 0.5;
}
.overlay.open a:hover {
  opacity: 1;
}
.overlay-wrapper {
  width: 90%;
  max-width: 1920px;
  margin: auto;
}
.active {
  color: #fff !important;
  opacity: 1 !important;
}
.social-icons {
  position: absolute;
  display: grid;
  grid-template-columns: repeat(3, 30px);
  justify-content: end;
  width: 90%;
  max-width: 1920px;
  grid-gap: 8px;
  margin: 0 auto;
  margin-top: 3rem;
}
@media only screen and (max-height: 500px) {
  .overlay.open li {
    font-size: calc(16px + (40 - 16) * ((100vw - 300px) / (1200 - 300)));
    font-weight: 500;
    list-style: none;
    padding-bottom: 10px;
  }
}
@media only screen and (min-width: 768px) {
  .social-icons {
    grid-template-columns: repeat(3, 40px);
    grid-gap: 16px;
  }
}
