.side-container {
  position: absolute;
  display: flex;
  width: 90%;
  max-width: 1920px;
  justify-content: flex-end;
  align-items: center;
  bottom: 17px;
  height: 17px;
  margin: 0 auto;
  color: #fff;
}
.side-container span {
  font-size: 9px;
  opacity: 0.5;
}
.copyright-icon {
  height: 10px;
  margin-right: 1px;
}
.react-icon {
  height: 20px;
}
@media only screen and (min-width: 768px) {
  .side-container {
    position: absolute;
    display: flex;
    width: inherit;
    justify-content: flex-end;
    align-items: center;
    bottom: 20px;
    height: 20px;
    color: #fff;
  }
  .side-container span {
    font-size: 12px;
    opacity: 0.5;
  }
  .copyright-icon {
    height: 14px;
    margin-right: 1px;
  }
  .react-icon {
    height: 24px;
  }
}
